:root {
  --priColor: #371f5e;
  --secColor: #dcf6ca;
}

@import url('https://fonts.googleapis.com/css?family=Montserrat');

body {
  margin: 5px;
}

.App {
  text-align: center;
  font-family: 'Montserrat';
}

.App-header {
  margin: 0;
  background-color: var(--priColor);
  min-height: 98.5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--secColor);
}

.button {
  position: absolute;
  display: flex;
  justify-content: space-evenly;
  top: 80vh;
  height: 5vh;
  width: 15vh;
}

.button > div {
  display: flex;
  align-items: center;
  text-align: center;
  background-color: var(--secColor);
  border-radius: 2px;
  padding: 0 6vh;
  color: var(--priColor);
  font-weight: 900;
  margin: 0 5vw;
}

.board {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
}

/* for the grid of 3x3 */
.board > input:nth-child(3n) {
  margin-right: 1.2vh;
}

.board > input:nth-child(27n) {
  margin-bottom: 1.4vh;
}
.board > input:nth-child(1 + 9n) {
  margin-left: 1.2vh;
}
